import i18n from "i18next";
import {initReactI18next} from "react-i18next";

import Backend from 'i18next-http-backend';
import LanguageDetector from 'i18next-browser-languagedetector';



import appTrans_uz from './uz/pages/AppNavbar';
import appTrans_ru from './ru/pages/AppNavbar';
import appTrans_en from './eng/pages/AppNavbar';
import menuTrans_uz from './uz/intoPages/MenuNavbar.json';
import menuTrans_ru from './ru/intoPages/MenuNavbar.json';
import menuTrans_en from './eng/intoPages/MenuNavbar.json';
import adminTrans_uz from './uz/admission/AdminNavbar.json';
import adminTrans_ru from './ru/admission/AdminNavbar.json';
import adminTrans_en from './eng/admission/AdminNavbar.json';
i18n

    .use(Backend)
    .use(LanguageDetector)
    .use(initReactI18next)

    .init({
        fallbackLng:'uz',
        debug:true,
        detection:['queryString','cookie'],
        cache:['cookie'],

        interpolation:{
            escapeValue:false,
            whitelist:['uz', 'ru','en'],
},
        lng:'uz',
        resources:{
            uz: {
                appTrans: appTrans_uz,
                menuTrans:menuTrans_uz,
                adminTrans:adminTrans_uz
            },
            ru: {
                appTrans: appTrans_ru,
                menuTrans:menuTrans_ru,
                adminTrans:adminTrans_ru
            },
            en:{
                appTrans: appTrans_en,
                menuTrans:menuTrans_en,
                adminTrans:adminTrans_en
            }
        }



    });

export default i18n;