import React, {Component} from 'react';
import '../../css/admissionStyle/Country.css';
// import * as BiIcons from "react-icons/bi";
import * as RiIcons from "react-icons/ri";
import * as FaIcons from "react-icons/fa";
// import * as MdIcons from "react-icons/md";
import axios from "axios";
import {Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";

import Switch from "react-switch";


import {BASE_URL} from '../../utils/config';
// export const PATH= BASE_URL+'/admin/country';

class News extends Component {
    constructor(props) {
        super(props);
        this.state = {
            countryDates: [],
            show: false,
            editShow: false,
            editId:0,
            checked:false,
            checkBox:this.checkBox.bind(this)

        }
    }

    checkBox=(checked)=>{
        this.setState({
            checked:checked
        })
    }

    openModal = (pos) => {
        if (pos === true) {
            this.setState({
                show: true
            })
        } else if (pos === false) {
            this.setState({
                show: false
            })
        }
    }
    editModal = (pos) => {
        if (pos === true) {
            this.setState({
                editShow: true
            })
        } else if (pos === false) {
            this.setState({
                editShow: false
            })
        }
    }


    queryCountry= () => {
        const _this = this;
        axios.get('http://localhost:8080/api/v1/admin/country').then(res =>_this.setState({countryDates:res.data.content}) )
    }

    addCountry = (event) => {
        event.preventDefault();
        axios({
            url: 'http://localhost:8080/api/v1/admin/country',
            method: 'put',
            data: {
                code:'code',
                names: {
                    uz: event.target[0].value,
                    en: event.target[1].value,
                    ru: event.target[2].value,
                },
                active: true
            }
        }).then(() => {

            this.queryCountry()
            this.openModal(false)
        })
    }

    editCountryIds=(id)=>{
        this.editModal(true)
        this.setState({
            editId:id
        })
    }
    editCountry = (event) => {
        event.preventDefault();
        const {editId}=this.state;
        axios({
            url: 'http://localhost:8080/api/v1/admin/country',
            method: 'post',
            data: {

                id:editId,
                code:'code',
                names: {
                    uz: event.target[0].value,
                    en: event.target[1].value,
                    ru: event.target[2].value,
                },
                active: true
            }
        }).then(() => {

            this.queryCountry()
            this.editModal(false)
        })
    }

    componentDidMount() {
        this.queryCountry();
    }


    render() {

        const {countryDates,show,editShow,checked}=this.state;

        const {t}=this.props;

        return (
            <div className={'countryLan'}>

                <Modal isOpen={show}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.openModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Mamlakat qo`shish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.addCountry}>
                            <label>
                                Uz
                            </label>
                            <input type="text" name={'uz'} className={'form-control'}/>
                            <label>
                                En
                            </label>
                            <input type="text" name={'en'} className={'form-control'}/>
                            <label>
                                Ru
                            </label>
                            <input type="text" name={'ru'} className={'form-control'}/>

                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.addBtn')}</button>

                            <button onClick={() => this.openModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>
                <Modal isOpen={editShow}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.editModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Mamlakatni o`zgartirish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.editCountry}>
                            <label>
                                Uz
                            </label>
                            <input type="text" name={'uz'} className={'form-control'}/>
                            <label>
                                En
                            </label>
                            <input type="text" name={'en'} className={'form-control'}/>
                            <label>
                                Ru
                            </label>
                            <input type="text" name={'ru'} className={'form-control'}/>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.editBtn')}</button>

                            <button onClick={() => this.editModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>

                <div className="cols">
                    <div className="row">
                        <div className="col mb-4">
                            <input type="text" className={'search'} placeholder={'Search...'}/>
                            <button onClick={()=>this.openModal(true)} className={'btn float-right'} style={{width: 100, fontSize: 12}}>
                                <RiIcons.RiFolderAddFill className={'mr-2'} style={{fontSize: 15}}/>
                                {t('admin.addBtn')}
                            </button>
                        </div>
                    </div>
                    <table className={'table text-center table-hover table-bordered '}>
                     <thead>
                     <tr>
                         <th></th>
                         <th>Uz</th>
                         <th>En</th>
                         <th>Ru</th>
                         <th> </th>
                         <th> </th>
                     </tr>
                     </thead>
                        <tbody>
                        {countryDates.map((item, index) => {
                         return(
                             <tr>
                                 <td style={{width: 50}}>{index+1}</td>
                                 <td>
                                     {item.names.uz}

                                 </td>
                                 <td>
                                     {item.names.en}
                                 </td>
                                 <td>
                                     {item.names.ru}
                                 </td>
                                 <td style={{width: 30}}>
                                     <button className={'btn'} onClick={()=>this.editCountryIds(item.id)}>
                                         <FaIcons.FaEdit/>
                                     </button>
                                 </td>
                                 <td style={{width: 30}}>
                                     <Switch onChange={()=>this.checkBox(item.active)} checked={item.active}/>
                                 </td>
                             </tr>
                         )
                        })}

                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default News;