import React, {Component} from 'react';
// import * as BiIcons from "react-icons/bi";
import * as ImIcons from "react-icons/im";
import * as MdIcons from "react-icons/md";
import * as FaIcons from "react-icons/fa";
// import {Link} from "react-router-dom";
// import {TabPanel} from "react-web-tabs";
import '../../css/admissionStyle/AdminPersons.css';
import * as RiIcons from "react-icons/ri";
import axios from "axios";
import {Modal, ModalBody, ModalHeader} from "reactstrap";
import {ModalFooter} from "react-bootstrap";

class AdminPersons extends Component {

    constructor(props) {
        super(props);
        this.state = {
            adminPerDates: [],
            adminRoleDates: [],
            show: false,
            editShow: false,
            editId:0,
        }
    }

    componentDidMount() {
        this.queryAdminPer();
        this.queryAdminPerRole();
    }

    openModal = (pos) => {
        if (pos === true) {
            this.setState({
                show: true
            })
        } else if (pos === false) {
            this.setState({
                show: false
            })
        }
    }

    editModal = (pos) => {
        if (pos === true) {
            this.setState({
                editShow: true
            })
        } else if (pos === false) {
            this.setState({
                editShow: false
            })
        }
    }

    queryAdminPer = () => {
        const _this = this;
        axios({
            url: 'http://localhost:8080/api/v1/admin/admin-member',
            method: 'get',
        }).then(function (res) {
            _this.setState({
                adminPerDates: res.data.content
            })
        });
    };

    queryAdminPerRole = () => {
        const _this = this;
        axios({
            url: 'http://localhost:8080/api/v1/admin/admin-member/roles',
            method: 'get',
        }).then(function (res) {
            _this.setState({
                adminRoleDates: res.data
            })
        });
    };

    addAdminPer = (event) => {
        event.preventDefault()
        axios({
            url: 'http://localhost:8080/api/v1/admin/admin-member',
            method: 'put',
            data: {
                firstName:event.target[0].value ,
                lastName: event.target[3].value,
                middleName:event.target[1].value,
                phone: event.target[4].value,
                password: event.target[2].value,
                role: {
                    id: event.target[5].value,
                }
            }
        }).then(() => {

            this.queryAdminPer();
            this.openModal(false)
        })
    };

    editAdminPerIds=(id)=>{
        this.editModal(true)
        this.setState({
            editId:id
        })
    }
    editAdminPer = (event) => {
        const {editId}=this.state;
        axios({
            url: 'http://localhost:8080/api/v1/admin/admin-member',
            method: 'post',
            data: {
                id:editId,
                firstName:event.target[0].value ,
                lastName: event.target[3].value,
                middleName:event.target[1].value,
                phone: event.target[4].value,
                password: event.target[2].value,
                role: {
                    id: event.target[5].value,
                }
            }
        }).then(() => {

            this.queryLang()
            this.editModal(false)
        })
    }

    render() {

        const {t}=this.props
        const {adminPerDates,adminRoleDates,editShow, show} = this.state


        return (
            <div className={'adminPersons'}>


                <Modal isOpen={show}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.openModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Admin qo`shish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.addAdminPer}>
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col">
                                        <label>
                                            Ism
                                        </label>
                                        <input type="text" name={'firstName'} className={'form-control mb-2'}/>
                                        <label>
                                            Otasining ismi
                                        </label>
                                        <input type="text" name={'middleName'} className={'form-control mb-2'}/>
                                        <label>
                                            Parol
                                        </label>
                                        <input type="password" name={'password'} className={'form-control mb-2'}/>
                                    </div>
                                    <div className="col">
                                        <label>
                                            Familiya
                                        </label>
                                        <input type="text" name={'lastName'} className={'form-control mb-2'}/>
                                        <label>
                                            Telefon raqami
                                        </label>
                                        <input type="text" name={'phone'} className={'form-control mb-2'}/>
                                        <label>
                                            Vazifasi
                                        </label>
                                        <select name="adminRole" className={'form-control'} id="">
                                            {adminRoleDates.map((item)=>{
                                                return(
                                                    <option className={'form-control'}   key={item.id} value={item.id}>
                                                        {item.name}
                                                    </option>
                                                )
                                            })}
                                        </select>

                                    </div>
                                </div>
                            </div>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.addBtn')}</button>

                            <button onClick={() => this.openModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>
                <Modal isOpen={editShow}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.editModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Adminni o`zgartirish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.editAdminPer}>
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col">
                                        <label>
                                            Ism
                                        </label>
                                        <input type="text" name={'firstName'} className={'form-control mb-2'}/>
                                        <label>
                                            Otasining ismi
                                        </label>
                                        <input type="text" name={'middleName'} className={'form-control mb-2'}/>
                                        <label>
                                            Parol
                                        </label>
                                        <input type="password" name={'password'} className={'form-control mb-2'}/>
                                    </div>
                                    <div className="col">
                                        <label>
                                            Familiya
                                        </label>
                                        <input type="text" name={'lastName'} className={'form-control mb-2'}/>
                                        <label>
                                            Telefon raqami
                                        </label>
                                        <input type="text" name={'phone'} className={'form-control mb-2'}/>
                                        <label>
                                            Vazifasi
                                        </label>
                                        <select name="adminRole" className={'form-control'} id="">
                                            {adminRoleDates.map((item)=>{
                                                return(
                                                    <option className={'form-control'}   key={item.id} value={item.id}>
                                                        {item.name}
                                                    </option>
                                                )
                                            })}
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.editBtn')}</button>

                            <button onClick={() => this.editModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>


                <div className="cols">
                    <div className="row">
                        <div className="col mb-4">
                            <input type="text" className={'search'} placeholder={'Search...'}/>
                            <button onClick={()=>this.openModal(true)} className={'btn float-right'} style={{width: 100, fontSize: 12}}>
                                <RiIcons.RiFolderAddFill className={'mr-2'} style={{fontSize: 15}}/>
                                {t('admin.addBtn')}
                            </button>
                        </div>
                    </div>
                    <table className={'table text-center table-hover table-bordered'}>
                       <thead>
                       <tr>
                           <th></th>
                           <th>F.I.O</th>
                           <th>Vazifasi</th>
                           <th> </th>
                           <th> </th>
                           <th> </th>
                       </tr>
                       </thead>
                        <tbody>
                        {adminPerDates.map((item, index) => {
                            return (
                                <tr>
                                    <td style={{width: 50}}>{index+1}</td>
                                    <td>
                                        {item.lastName} {item.firstName} {item.middleName}
                                    </td>
                                    <td>
                                        {item.role.name}
                                    </td>
                                    <td  style={{width:50}}>
                                        <button className={'btn'}>
                                            <ImIcons.ImKey />
                                        </button>
                                    </td>
                                    <td style={{width: 30}}>
                                        <button className={'btn'} onClick={()=>this.editAdminPerIds(item.id)}>
                                            <FaIcons.FaEdit/>
                                        </button>
                                    </td>
                                    <td style={{width: 30}}>
                                        <button className={'btn'}>
                                            <MdIcons.MdDelete/>
                                        </button>
                                    </td>
                                </tr>
                            )
                        })}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default AdminPersons;