import React, {Component} from 'react';
import * as RiIcons from "react-icons/ri";
import * as FaIcons from "react-icons/fa";
import '../../css/admissionStyle/Regions.css';
import {Modal, ModalBody, ModalFooter, ModalHeader} from "reactstrap";
import Switch from "react-switch";
import axios from "axios";
class Regions extends Component {

    constructor(props) {
        super(props);
        this.state = {
            regionDates: [],
            countrySelect:[],
            show: false,
            editShow: false,
            editId:0,
            checked:false,
            checkBox:this.checkBox.bind(this)

        }
    }

    checkBox=(checked)=>{
        this.setState({
            checked:checked
        })
    }

    openModal = (pos) => {
        if (pos === true) {
            this.setState({
                show: true
            })
        } else if (pos === false) {
            this.setState({
                show: false
            })
        }
    }
    editModal = (pos) => {
        if (pos === true) {
            this.setState({
                editShow: true
            })
        } else if (pos === false) {
            this.setState({
                editShow: false
            })
        }
    }


    queryRegion= () => {
        const _this = this;
        axios.get('http://localhost:8080/api/v1/admin/region').then(res =>_this.setState({regionDates:res.data.content}) )
    }

    queryCountry= () => {
        const _this = this;
        axios.get('http://localhost:8080/api/v1/admin/country').then(res =>_this.setState({countrySelect:res.data.content}) )
    }

    addRegion = (event) => {
        event.preventDefault();
        axios({
            url: 'http://localhost:8080/api/v1/admin/region',
            method: 'put',
            data: {
                code:'code',
                names: {
                    uz: event.target[1].value,
                    en: event.target[2].value,
                    ru: event.target[3].value

                },
                active: true,
                "country": {
                    "id": event.target[0].value,
                }
            }
        }).then(() => {

            this.queryRegion()
            this.openModal(false)
        })
    }

    editRegionIds=(id)=>{
        this.editModal(true)
        this.setState({
            editId:id
        })
    }
    editRegion = (event) => {
        const {editId}=this.state;
        event.preventDefault();
        axios({
            url: 'http://localhost:8080/api/v1/admin/region',
            method: 'post',
            data: {
                id:editId,
                code:'codes',
                names: {
                    uz: event.target[1].value,
                    en: event.target[2].value,
                    ru: event.target[3].value

                },
                "active": true,
                "country": {
                    "id": event.target[0].value,
                }
            }
        }).then(() => {

            this.queryRegion()
            this.editModal(false)
        })
    }

    componentDidMount() {
        this.queryRegion();
        this.queryCountry();
    }


    render() {
        const {t}=this.props
        const {regionDates, countrySelect ,show,editShow,checked}=this.state;
        return (
            <div className={'regions'}>
                <Modal isOpen={show}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.openModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Viloyat qo`shish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.addRegion}>
                            <label>
                                Mamlakat
                            </label>
                            <select name="select" className={'form-control mb-3'} id="">
                                {countrySelect.map((item, index) => {
                                    return(
                                        <option className={'form-control'} key={item.id} value={item.id}>{item.names.uz}</option>
                                    )
                                })}
                            </select>
                            <label>
                                Uz
                            </label>
                            <input type="text" name={'uz'} className={'form-control mb-3'}/>
                            <label>
                                En
                            </label>
                            <input type="text" name={'en'} className={'form-control mb-3'}/>
                            <label>
                                Ru
                            </label>
                            <input type="text" name={'ru'} className={'form-control mb-3'}/>
                        </form>

                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.addBtn')}</button>

                            <button onClick={() => this.openModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>
                <Modal isOpen={editShow}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.editModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Viloyatni o`zgartirish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.editRegion}>
                            <label>
                                Mamlakat
                            </label>
                            <select name="select" className={'form-control mb-3'} id="">
                                {countrySelect.map((item, index) => {
                                    return(
                                        <option className={'form-control'} key={item.id} value={item.id}>{item.names.uz}</option>
                                    )
                                })}
                            </select>
                            <label>
                                Uz
                            </label>
                            <input type="text" name={'uz'} className={'form-control mb-3'}/>
                            <label>
                                En
                            </label>
                            <input type="text" name={'en'} className={'form-control mb-3'}/>
                            <label>
                                Ru
                            </label>
                            <input type="text" name={'ru'} className={'form-control mb-3'}/>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.editBtn')}</button>
                            <button onClick={() => this.editModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>



                <div className="cols">
                    <div className="row">
                        <div className="col mb-4">
                            <input type="text" className={'search'} placeholder={'Search...'}/>
                            <button onClick={()=>this.openModal(true)} className={'btn float-right'} style={{width: 100, fontSize: 12}}>
                                <RiIcons.RiFolderAddFill className={'mr-2'} style={{fontSize: 15}}/>
                                {t('admin.addBtn')}
                            </button>
                        </div>
                    </div>
                    <table className={'table text-center table-hover table-bordered'}>
                     <thead>
                     <tr>
                         <th></th>
                         <th>Uz</th>
                         <th>En</th>
                         <th>Ru</th>
                         <th> </th>
                         <th> </th>
                         <th> </th>
                     </tr>
                     </thead>
                        <tbody>
                        {regionDates.map((item, index) => {
                            return(
                                <tr>
                                    <td style={{width: 50}}>{index+1}</td>
                                    <td>
                                        {item.names.uz}
                                    </td>
                                    <td>
                                        {item.names.en}
                                    </td>
                                    <td>
                                        {item.names.ru}
                                    </td>
                                    <td>
                                        {item.country.id}
                                    </td>
                                    <td style={{width: 30}}>
                                        <button className={'btn'} onClick={()=>this.editRegionIds(item.id)}>
                                            <FaIcons.FaEdit/>
                                        </button>
                                    </td>
                                    <td style={{width: 30}}>
                                        <Switch onChange={this.checkBox} checked={item.active}/>
                                    </td>
                                </tr>
                            )
                        })}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default Regions;