import React, {Component} from 'react';
import * as FaIcons from "react-icons/fa";
import * as MdIcons from "react-icons/md";

import '../../css/admissionStyle/EducationLan.css';
import * as RiIcons from "react-icons/ri";
import axios from 'axios'
import {Modal, ModalBody, ModalFooter, ModalHeader,} from 'reactstrap';

// import {BASE_URL} from '../../utils/config';

// export const PATH= BASE_URL+'/admin/education-language';


class EducationLan extends Component {

    constructor(props) {
        super(props);
        this.state = {
            langDates: [],
            show: false,
            editShow: false,
            editId:0,

        }
    }
    componentDidMount() {

        this.queryLang();
    }

    openModal = (pos) => {
        if (pos === true) {
            this.setState({
                show: true
            })
        } else if (pos === false) {
            this.setState({
                show: false
            })
        }
    }
    editModal = (pos) => {
        if (pos === true) {
            this.setState({
                editShow: true
            })
        } else if (pos === false) {
            this.setState({
                editShow: false
            })
        }
    }

    queryLang = () => {
        const _this = this;
        axios({
            url: 'http://localhost:8080/api/v1/admin/education-language',
            method: 'get',
        }).then(function (res) {
            _this.setState({
                langDates: res.data.content
            })
        });
    }

    addLang = (event) => {
        event.preventDefault();
        axios({
            url: 'http://localhost:8080/api/v1/admin/education-language',
            method: 'put',
            data: {
                names: {
                    uz: event.target[0].value,
                    en: event.target[1].value,
                    ru: event.target[2].value,
                },
                active: true
            }
        }).then(() => {

            this.queryLang();
            this.openModal(false)
        })
    }

    editLangIds=(id)=>{
        this.editModal(true)
        this.setState({
            editId:id
        })
    }
    editLang = (event) => {
        event.preventDefault()
        const {editId}=this.state;
        axios({
            url: 'http://localhost:8080/api/v1/admin/education-language/',
            method: 'post',
            data: {
                id:editId,
                   names: {
                       uz: event.target[0].value,
                       en: event.target[1].value,
                       ru: event.target[2].value,
                  },
                active: true
            }
        }).then(() => {
            this.editModal(false)
            this.queryLang()
        })
    }




    render() {

        const {t}=this.props;
        const {langDates,show,editShow}=this.state

        return (
            <div className={'languages'}>

                <Modal isOpen={show}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.openModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Ta`lim tilini qo`shish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.addLang}>
                            <label>
                                Uz
                            </label>
                            <input type="text" name={'uz'} className={'form-control'}/>
                            <label>
                                En
                            </label>
                            <input type="text" name={'en'} className={'form-control'}/>
                            <label>
                                Ru
                            </label>
                            <input type="text" name={'ru'} className={'form-control'}/>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.addBtn')}</button>

                            <button onClick={() => this.openModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>
                <Modal isOpen={editShow}>
                    <header>
                        <button className={'btn float-lg-right'} style={{fontSize:18}} onClick={()=>this.editModal(false)}>&times;</button>
                        <h3 className={'m-3'}>Ta`lim tilini o`zgartirish</h3>
                    </header>
                    <ModalBody>
                        <form id={'lang'} onSubmit={this.editLang}>
                            <label>
                                Uz
                            </label>
                            <input type="text" name={'uz'} className={'form-control'}/>
                            <label>
                                En
                            </label>
                            <input type="text" name={'en'} className={'form-control'}/>
                            <label>
                                Ru
                            </label>
                            <input type="text" name={'ru'} className={'form-control'}/>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <div className={'float-right'}>
                            <button form={'lang'} className={' btn btn-success'}>{t('admin.editBtn')}</button>
                            <button onClick={() => this.editModal(false)} className={'btn btn-success'}>{t('admin.closeBtn')}</button>
                        </div>
                    </ModalFooter>
                </Modal>


                <div className="cols">
                    <div className="row">
                        <div className="col mb-4">
                            <input type="text" className={'search'} placeholder={'Search...'}/>
                            <button onClick={()=>this.openModal(true)} className={'btn float-right'} style={{width: 100, fontSize: 12}}>
                                <RiIcons.RiFolderAddFill className={'mr-2'} style={{fontSize: 15,width:100}}/>
                                {t('admin.addBtn')}
                            </button>
                        </div>
                    </div>



                    <table className={'table text-center table-hover table-bordered'}>
                        <thead>
                        <tr>
                            <th></th>
                            <th>Uz</th>
                            <th>En</th>
                            <th>Ru</th>
                            <th>&nbsp;</th>
                            <th>&nbsp;</th>
                        </tr>
                        </thead>
                        <tbody>
                        {langDates.map((item, index) => {
                            return (
                                <tr>
                                    <td style={{width: 50}}>{index+1}</td>
                                    <td>
                                        {item.names.en}
                                    </td>
                                    <td>
                                        {item.names.ru}
                                    </td>
                                    <td>
                                        {item.names.uz}

                                    </td>
                                    <td style={{width: 30}}>
                                        <button className={'btn'} onClick={()=>this.editLangIds(item.id)}>
                                            <FaIcons.FaEdit/>
                                        </button>
                                    </td>
                                    <td style={{width: 30}}>
                                        <button className={'btn'}>
                                            <MdIcons.MdDelete/>
                                        </button>
                                    </td>
                                </tr>
                            )
                        })}


                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default EducationLan;